.contact {
  padding-bottom: 100px;

  &__main {
    width:630px;
    margin: -290px auto 0;
    padding: 75px 60px 40px 50px;
    background:rgba(255,255,255,1);
    border-radius:4px;
    text-align: center;
  }

  &__h1 {
    margin-bottom: 30px;
    font-size:60px;
    font-weight:bold;
    color:rgba(51,51,51,1);
    line-height: 1;
  }

  &__tips {
    margin-bottom: 40px;
    font-weight:400;
  }

  &__form {
    &-item {
      position: relative;
      display: flex;

      label.error {
        position: absolute;
        right: 20px;
        top: 0;
        color: #F5333D;
        font-size: 12px;
        font-weight: 400;
        line-height: 52px;
      }
    }

    &-required {
      position: absolute;
      top: 0;
      left: 0;
      font-size:16px;
      font-weight:400;
      line-height: 52px;
    }

    &-input {
      margin-left: 20px;
    }
  }

  &__select {
    position: relative;

    &-icon {
      position: absolute;
      top: 50%;
      right: 20px;
      transform: translateY(-50%);
      color: #333;
      font-size: 12px;
      transform-origin: 50% 50%;
      transition: all .5s;

      &.reversed {
        transform: translateY(-50%) rotate(-180deg);
      }
    }

    .contact__form-input {
      padding-right: 42px;
    }

    label.error {
      right: 42px;
    }
  }

  &__dropdown {
    min-width: calc(100% - 20px);
    background: #F9F9F9;
    border-color: #F9F9F9;

    &-item {
      padding: 15.5px 20px;
    }
  }

  &__textarea {
    resize: none;
    height: 160px !important;
  }

  &__submit {
    margin-top: 28px;
  }
}

@media (max-width: 646px) {
  .contact {
    &__main {
      width: 100%;
      padding-left: 20px;
      padding-right: 30px;
    }
  }
}